import React from 'react'
import { StyleRoot } from 'radium'
import { connect } from  'react-redux'

import Services from '../../components/aboutMe/services'
import Skills from '../../components/aboutMe/skills'


const mapStateToProps = state => {
  return state
}


class aboutMe extends React.Component{
    constructor(props){
      super(props);
      
    }
    
    
    render(){
      var AnimationStyles = require('../../utils/animation')
        return(
            <StyleRoot>
              {this.props.main.details ?
                 <div role="tabpanel" className="tab-pane fade in active" id="about-me" style={AnimationStyles.styles.bounce_in_right}>
                    <div className="inside-sec">
                 <h5 className="tittle" style={AnimationStyles.styles.fade_in_up}>About Me</h5>
                      {/* Blog */}
                      <section className="about-me padding-top-10" style={AnimationStyles.styles.fade_in_up}> 
                        {/* Personal Info */}
                        <ul className="personal-info">
                          <li>
                            <p> <span> Name</span> {this.props.main.details.name} </p>
                          </li>
                          <li>
                            <p> <span> Age</span> {this.props.main.details.age} </p>
                          </li>
                          <li>
                            <p> <span> Location</span> {this.props.main.details.city}, {this.props.main.details.state} </p>
                          </li>
                          <li>
                            <p> <span> Experience</span> {this.props.main.details.experience} </p>
                          </li>
                          <li>
                            <p> <span> Degree</span> {this.props.main.details.degree} </p>
                          </li>
                          <li>
                            <p> <span> Career Level</span> {this.props.main.details.careerLevel} </p>
                          </li>
                          <li>
                            <p> <span> Phone</span> 
                              <a href={`tel:${this.props.main.details.phone}`}> 
                                  {this.props.main.details.parsedPhone}
                              </a> 
                            </p>
                          </li>
                          <li>
                            <p> 
                              <span> E-Mail</span> 
                              <a href={`mailto:${this.props.main.details.email}`}>{this.props.main.details.email}</a>
                            </p>
                          </li>
                          <li>
                            <p> <span> Website</span><a href={this.props.main.details.website}> {this.props.main.details.website} </a></p>
                          </li>
                        </ul>
                        {/* Im Web Designer */}
                        <h5 className="tittle">Web Developer &amp; Network Admin</h5>
                        <div className="padding-20">
                          {/*Bio Goes Here*/}
                          {this.props.main.details.bioHTML ?
                            <div dangerouslySetInnerHTML={this.props.main.details.bioHTML}/> : <div/>
                          }
                        </div>
                        {/*Services Go Here*/}
                        <Services />
                        {/* Skills */}
                        <Skills/>
                      </section>
                      </div>
                      </div> : <div/>
              }
            </StyleRoot>
        )
    }
}
export default connect(mapStateToProps)(aboutMe)